<template>
  <div>
    <!-- 标签页 navBar -->
    <van-nav-bar left-arrow @click-left="$router.push('/home')" title="分类" />
      <!-- 侧边导航 sidebar -->
    <van-sidebar v-model="activeKey">
      <van-sidebar-item v-for="(item,index) in firstCate" :key="item.id" :title="item.catename" @click="changeCate(index)" />
    </van-sidebar>
    <div class="main">
        <van-grid :border="false" :column-num="3">
          <van-grid-item v-for="item in secondCate" :key="item.id" :to="`/goodsList?fid=${item.id}`">
            <van-image :src="$preImg+item.img" />
            <p>{{item.catename}}</p>
          </van-grid-item>

        </van-grid>
    </div>
  </div>
</template>
<script>
import {getCate} from '../utils/request'
export default {
  data(){
    return {
      activeKey:0,
      // 一级分类列表
      firstCate:[],
      // 二级分类列表
      secondCate:[],
    }
  },
  methods:{
    requestCate(){
      getCate().then(res=>{
        this.firstCate = res.data.list
        // console.log(this.firstCate);
        this.changeCate(0)
      })
    },
    // 获取二级列表
    changeCate(index){
      this.secondCate = this.firstCate[index].children;
    }
  },
  mounted(){
    // 发起分类的请求
    this.requestCate();
  }
}
</script>

<style scoped>
  .main{
    position: absolute;
    top: 55px;
    right: 0;
    margin-left: 80px;
  }
</style>


